/* Header */
#header{
    width: 100%;
    height: 10vh;
    overflow-x: hidden;
    display: flex;
}
#logo_img{
    width:50%;
    margin-top: 10px;
}
#hamburger_container{
    width: 35%;

}
.header_containers{
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo{
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.hamburger_menu{
    width: 100%;
    height: 2px;
    background-color: white;
    margin: 3px 0px 0px 0px;
}

